body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 8vmin;
  margin-bottom: 5vmin;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
  -webkit-animation: Logo-fly-up 3s;
          animation: Logo-fly-up 3s;
}

.App-logo-wide {
  height: 8vmin;
  -webkit-animation: Fade-in 5s;
          animation: Fade-in 5s;
}

.Hero-screen {
  min-height: 100vh;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.Full-page-container{
  min-height: 100vh;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  background: -webkit-linear-gradient(#0F0929,#27213E);
  background: linear-gradient(#0F0929,#27213E);
}

.Particles-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  background: -webkit-radial-gradient(#0F0929e8 1%,#0F0929 40%);
  background: radial-gradient(#0F0929e8 1%,#0F0929 40%);
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: -1;
  padding-bottom: 6vmin;
}

.Particles {
  position: absolute;
  width: 100%;
  height: 100vh;
  z-index: -1;
}

.Down-arrow-container{
  align-self: flex-end;
  padding-bottom: 6vmin;
  -webkit-animation: Fade-in 8s;
          animation: Fade-in 8s;
}

.Down-arrow{
  height: 6vmin;
  -webkit-animation: Down-arrow-bounce infinite 1.5s linear;
          animation: Down-arrow-bounce infinite 1.5s linear;
}

.Sub-header {
  font-family: "Open Sans Light",serif;
  font-weight: 300;
  color:white;
  -webkit-animation: Fade-in 7s;
          animation: Fade-in 7s;
}

.Card-grid{
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

@-webkit-keyframes Down-arrow-bounce {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  80% {
    -webkit-transform: translateY(2vmin);
            transform: translateY(2vmin);
  }
  100%{
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes Down-arrow-bounce {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  80% {
    -webkit-transform: translateY(2vmin);
            transform: translateY(2vmin);
  }
  100%{
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@-webkit-keyframes Logo-fly-up {
  0% {
    -webkit-transform: translateY(10vmin) scale(0.5,0.5);
            transform: translateY(10vmin) scale(0.5,0.5);
  }

  25% {
    -webkit-transform: translateY(10vmin) scale(3,3);
            transform: translateY(10vmin) scale(3,3);
  }

  50% {
    -webkit-transform: translateY(10vmin) scale(3,3);
            transform: translateY(10vmin) scale(3,3);
  }

  100% {
    -webkit-transform: translateY(0) scale(1,1);
            transform: translateY(0) scale(1,1);
  }
}

@keyframes Logo-fly-up {
  0% {
    -webkit-transform: translateY(10vmin) scale(0.5,0.5);
            transform: translateY(10vmin) scale(0.5,0.5);
  }

  25% {
    -webkit-transform: translateY(10vmin) scale(3,3);
            transform: translateY(10vmin) scale(3,3);
  }

  50% {
    -webkit-transform: translateY(10vmin) scale(3,3);
            transform: translateY(10vmin) scale(3,3);
  }

  100% {
    -webkit-transform: translateY(0) scale(1,1);
            transform: translateY(0) scale(1,1);
  }
}

@-webkit-keyframes Fade-in {
  0% {
    opacity: 0;
  }

  50% {
    opacity: 0;
  }

  100%{
    opacity: 100%;
  }
}

@keyframes Fade-in {
  0% {
    opacity: 0;
  }

  50% {
    opacity: 0;
  }

  100%{
    opacity: 100%;
  }
}
.Card-container{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    width: 20vmax;
    min-width: 215px;
    margin: 5vmin 2.5vmin;
}

.Card-image{
    width: 100%;
}

.Card-header{
    text-align: left;
    width: 100%;
    min-font-size: 25px;
    font-size: 2.5vh;
    letter-spacing: 0.3vh;
    font-family: "Open Sans", serif;
    color: #FBDDFB;
}

.Card-description{
    margin: 0;
    text-align: left;
    width: 100%;
    font-size: 1.5vh;
    font-family: "Open Sans Light", serif;
    color: rgba(255,255,255,0.7);
}
